import React from "react";
import "./App.css";
import Home from "./components/Home/Home";
import Slide1 from "./components/Slide1/Slide1";
import ivdh from "./images/ivdh.png";
import hydrautronics from "./images/hydrautronics.png";
import { ReactComponent as Logo } from "./images/logo.svg";
import Contact from "./components/Contact/Contact";

function App() {
  // function voor smooth scrolling
  function smoothScroll(event) {
    event.preventDefault();
    const navigationHeight = 0;

    let targetPosition = 0;
    console.log(
      document.querySelector(event.target.dataset.target)
      //.getBoundingClientRect().top
    );
    if (event.target.dataset.target === "#home") {
      targetPosition = 0;
    } else {
      targetPosition =
        document.querySelector(event.target.dataset.target).offsetTop -
        navigationHeight;
    }
    const startPosition = window.pageYOffset;
    let distance = targetPosition - startPosition;
    const duration = 800;
    let start = null;

    console.log("from: " + window.pageYOffset + ", to: " + targetPosition);
    console.log("duration: " + duration);

    // animatie
    window.requestAnimationFrame(step);

    function step(timestamp) {
      if (!start) start = timestamp;
      const progress = timestamp - start;
      window.scrollTo(
        0,
        easeOutCubic(progress, startPosition, distance, duration)
      );
      if (progress < duration) {
        window.requestAnimationFrame(step);
      }
    }

    // timing function
    function easeOutCubic(t, b, c, d) {
      t /= d;
      t--;
      return -c * (t * t * t * t - 1) + b;
    }

    let tempColor = "";
    if (document.querySelector(".menu").classList.contains("visible")) {
      document.querySelector(".menu").classList.remove("visible");
      document.querySelector(".nav__button").classList.remove("active");
      if (tempColor === "dark") {
        tempColor = "";
        document.body.classList.add("dark");
      }
    } else {
      document.querySelector(".menu").classList.add("visible");
      document.querySelector(".nav__button").classList.add("active");
      if (document.body.classList.contains("dark")) {
        tempColor = "dark";
        document.body.classList.remove("dark");
      }
    }
  }

  return (
    <div className="App">
      <nav>
        <Logo />
        <div className="nav__button">
          <div className="inner">
            <div className="stripe1"></div>
            <div className="stripe2"></div>
          </div>
        </div>
      </nav>
      <div className="menu">
        <div className="inner">
          <p className="menu__link" data-target="#home" onClick={smoothScroll}>
            Portfolio
          </p>
          <p
            className="menu__link"
            data-target="#contact"
            onClick={smoothScroll}
          >
            Contact
          </p>
        </div>
      </div>
      <Home />
      <Slide1
        image={ivdh}
        title="Ivdh Interieurontwerp"
        subtitle="HTML • CSS • JS"
        paragraph="Ivdh Interieurontwerp ontwerpt interieurs voor particulieren en
            bedrijven."
        code=""
        website="https://ivdhinterieurontwerp.nl"
      />
      <Slide1
        image={hydrautronics}
        title="Hydrautronics"
        subtitle="UI UPDATE WORDPRESS"
        paragraph="Hydrautronics maakt hydraulische systemen
voor opdrachtgevers in meer dan 30 landen."
        code=""
        website="https://hydrautronics.eu"
      />
      <Contact />

      <div className="animation">
        <Logo />
      </div>
    </div>
  );
}

export default App;
