import React from "react";
import ReactDOM from "react-dom";
import "./index.css";
import App from "./App";
import * as serviceWorker from "./serviceWorker";
// import { classBody } from "@babel/types";

ReactDOM.render(<App />, document.getElementById("root"));

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();

window.addEventListener("scroll", windowScroll);
const backgrounds = [
  {
    background: "#ffffff",
    color: "#333333",
    subtitle: "#333333",
    body: "light"
  },
  {
    background: "#333333",
    color: "white",
    subtitle: "#B38659",
    body: "dark"
  },
  {
    background: "#8FACCC",
    color: "#333333",
    subtitle: "#335880",
    body: "light"
  },
  {
    background: "#ffffff",
    color: "#333333",
    subtitle: "#333333",
    body: "light"
  }
];

let lastPos;
let tempColor = "";
function windowScroll(event) {
  const scrolled = window.pageYOffset;
  let current = backgrounds[0];

  let direction = "down";
  if (lastPos > scrolled) {
    direction = "up";
  }
  lastPos = window.pageYOffset;

  if (
    direction === "down" &&
    document.querySelector(".scroll > img").style.opacity > 0
  ) {
    document.querySelector(".scroll > img").style.opacity =
      1 - (scrolled / window.innerHeight) * 4;
    document.querySelector(".scroll > p").style.opacity =
      1 - (scrolled / window.innerHeight) * 4;
  } else if (
    direction === "up" &&
    document.querySelector(".scroll > img").style.opacity < 1
  ) {
    document.querySelector(".scroll > img").style.opacity =
      1 - (scrolled / window.innerHeight) * 4;
    document.querySelector(".scroll > p").style.opacity =
      1 - (scrolled / window.innerHeight) * 4;
  }

  for (let i = 0; i < backgrounds.length; i++) {
    if (i * window.innerHeight + window.innerHeight / 2 < scrolled) {
      if (backgrounds[i + 1]) {
        current = backgrounds[i + 1];
      }
    }
  }

  // updating styles
  if (document.body.style.backgroundColor !== current.background) {
    document.body.style.backgroundColor = current.background;
  }
  if (document.querySelector(".subtitle").style.color !== current.subtitle) {
    document.querySelectorAll(".subtitle").forEach(element => {
      element.style.color = current.subtitle;
    });
  }

  if (
    current.body === "dark" &&
    document.querySelector(".menu").classList.contains("visible")
  ) {
    tempColor = "dark";
  } else if (current.body === "dark") {
    document.body.classList.add("dark");
  } else {
    tempColor = "";
    document.body.classList.remove("dark");
  }
}

document.querySelector(".nav__button").addEventListener("click", showMenu);

function showMenu(event) {
  if (document.querySelector(".menu").classList.contains("visible")) {
    document.querySelector(".menu").classList.remove("visible");
    document.querySelector(".nav__button").classList.remove("active");
    if (tempColor === "dark") {
      tempColor = "";
      document.body.classList.add("dark");
    }
  } else {
    document.querySelector(".menu").classList.add("visible");
    document.querySelector(".nav__button").classList.add("active");
    if (document.body.classList.contains("dark")) {
      tempColor = "dark";
      document.body.classList.remove("dark");
    }
  }
}
